import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Particle from "../Particle";
import Resumecontent from "./ResumeContent";
import "../../style.css";
import "bootstrap/dist/css/bootstrap.min.css";
import axios from "axios";

function Resume() {
  const uri = "https://athulrajtheno1.github.io/sem/";
  const [spojRank, upadteSpojRank] = useState(0);
  const [hackerrank, upadteHackerank] = useState(0);
  const [sem, upadateSem] = useState(0);
  const [cgpa, upadteCgpa] = useState(0);

  useEffect(() => {
    axios
      .get(uri)
      .then((res) => {
        upadteSpojRank(res.data.message[0].spojRank);
        upadteHackerank(res.data.message[1].hackerrank);
        upadteCgpa(res.data.message[2].cgpa);
        upadateSem(res.data.message[3].sem);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <Container fluid className="resume-section">
      <Particle />
      <Container>
        <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button variant="primary" href="https://discord.gg/wj2NhEAUja">
            <i className="fas fa-download">&nbsp;</i>Join Discord
          </Button>
        </Row>
        <Row className="resume">
          <Col md={6} className="resume-left">
            
            <h3 className="resume-title">Events Participated</h3>
            <Resumecontent
              title="Coordinated IETE Students Day"
              content={[
                "I was assigned the role of an anchor for the music competition.",
              ]}
            />
            <Resumecontent
              title="Portfolio Developement Competition"
              content={[
                "I had enrolled in a portfolio making competition by HackClub KMEA in which i had to make a website using React.js",
              ]}
            />
          </Col>
          <Col md={6} className="resume-right">
            <h3 className="resume-title">Education</h3>
            <Resumecontent
              title="KMEA Engineering College"
              date="2020 - 2024"
              content={[`Electronics & Communication : Sem 2`]}
            />
            <Resumecontent
              title="12TH BOARD [Cardinal HSS]"
              date="2018 - 2020"
              content={["Precentage: 93%"]}
            />
            <Resumecontent
              title="10TH BOARD [Nava Nirman Public School] "
              date="2006-2018"
              content={["Precentage: 90%"]}
            />

<h3 className="resume-title">Ranks and Achivements</h3>
            <Resumecontent
              title=""
              content={[
                `HackClub Member KMEA`,
                `Blue Belt in Karate`,
                `Allrounder of the year 2016 in Nava Nirman Public School`,
                "Full A+ in +2",
                "Won Several Prizes for Poetry Competition in English, Malayalam and Hindi"
              ]}
            />

          </Col>
        </Row>
        <Row style={{ justifyContent: "center", position: "relative" }}>
          
        </Row>
      </Container>
    </Container>
  );
}

export default Resume;
